
    // 容器斷點設定
        // PC 
        @mixin pc {
            @media(min-width: 1200px){
                @content;
            }
        }
        // Table
        @mixin table {
            @media (min-width: 768px){
                @content;
            }
        }
    // ------------
    // button-size
    .btn-md {
        padding: 12px 20px !important; 
        @include table {
            padding: 12px 13px !important;
        }
        @include pc {
            padding: 12px 48px !important;
        }
    }
    // ---------button-size end------

    // reservation
    .reservation {
        padding-top: 48px;
        padding-bottom: 88px;
        @include table {
            padding-top: 64px;
            padding-bottom: 80px;
        }
        @include pc {
            padding-top: 64px;
            padding-bottom: 104px;
        }
    }
    .reservation_form {
        padding-top: 24px;
        @include table {
            padding-top: 27px;
        }
        @include pc {
            padding-top: 35px;
        }
        input:checked {
            color: #907A7B;
        }
    }